
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { ForbiddenErrorNotification } from "@/types/notifications";

@Component
export default class ForbiddenError extends Vue {
  @Prop()
  value!: ForbiddenErrorNotification;

  get errorMessage(): string {
    return (
      this.value.errorMessage ??
      "You do not have sufficient permissions to perform this action. Try disabling your security plugin or contact your hosting provider for further assistance."
    );
  }
}
